* {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
  box-sizing: border-box;
}

.main {
  width: 100%;
  height: 100%;
  height: 100vh;
  /* min-height: 100vh; */
  /* max-height: 100vh; */
  display: flex;
  flex-direction: column;
  /* border: 1px solid red; */
}

/*모바일 세로*/
@media screen and (max-width: 479.99px) {
  nav {
    flex: 0 0 90px;
  }
  aside {
    flex: 0 0 35px;
    margin-bottom: 0.2rem;
    /* border: 1px solid red; */
  }
  .section {
    overflow: auto;
    margin-bottom: 3px;
  }
  .show-section {
    flex: 1;
  }
  .addItems {
    flex: 0 0 170px;
    margin-bottom: 2px;
  }
}
/*모바일 가로 & 태블릿 세로*/
@media screen and (min-width: 480px) {
  nav {
    flex: 0 0 90px;
  }
  aside {
    flex: 0 0 37.5px;
    /* border: 1px solid red; */
  }
  .section {
    overflow: auto;
    margin-bottom: 3px;
  }
  .show-section {
    flex: 1;
  }
  .addItems {
    flex: 0 0 170px;
    margin-bottom: 2px;
  }
}
/*태블릿 가로*/
@media screen and (min-width: 768px) {
  nav {
    flex: 0 0 110px;
  }
  aside {
    flex: 0 0 40px;
  }
  .section {
    overflow: auto;
    margin-bottom: 3px;
  }
  .show-section {
    flex: 1;
  }
  .addItems {
    flex: 0 0 220px;
  }
}
/*노트북 & 태블릿 가로*/
@media screen and (min-width: 1024px) {
  .main {
    min-height: 100vh;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-auto-rows: 130px 1fr;
  }

  nav {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
  }

  .show-main {
    grid-auto-rows: 130px 1fr 230px;
  }

  aside {
    grid-column: 1 / 3;
    grid-row: 2 / 4;

    padding-top: 0.125rem;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.25rem;
  }
  .section {
    grid-column: 3 / -1;
    grid-row: 2 / 4;
  }
  .show-section {
    grid-column: 3 / -1;
    grid-row: 2 / 3;
  }
  .addItems {
    grid-column: 3 / -1;
    grid-row: 3 / 4;
  }
}
