/*addItems 자식*/
.addItems {
  padding: 0.125rem;
}
.addItems-container {
  width: 100%;
  height: 100%;
  display: flex;
  background-color: #fff;
  border: 3px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;

  /* border: 1px solid red; */
}
.addItems-center {
  flex: 1;
  display: flex;
  overflow: hidden;
  /* border: 1px solid blue; */
}
.addItem-move {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
  transition: all 0.3s linear;
  /* border: 1px solid red; */
}
.addItem-move:hover {
  background-color: black;
  color: #fff;
}

.addItem-list::-webkit-scrollbar {
  display: none;
}
.addItem-list {
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}
.addItem-list {
  flex: 1;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  height: 100%;
  background-color: #f0f0f0;
}
.addItem-article {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(34, 34, 34, 0.5);
  border-radius: 0.15rem;
  background-color: #fff;
  text-align: center;
}
.addItem-article:hover {
  border: 1px solid rgba(0, 0, 0, 1);
}
.lastSlide {
  transform: translateX(-100%);
  display: none;
}
.addItem-remove {
  position: absolute;
  display: flex;
  justify-content: center;
  cursor: pointer;
  background-color: transparent;
  border-color: transparent;
  color: #000;
  opacity: 0.8;
  z-index: 99;
}
.addItem-remove:hover {
  opacity: 1;
}
.addItem-item {
  position: relative;
  /* flex: 1 0 auto; */
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid red; */
}
.addItem-img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.addItem-title {
  flex: 0 0 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid red; */
}
.addItem-btn {
  flex: 0 0 20%;
  display: flex;
  justify-content: space-evenly;
  /* border: 1px solid red; */
}
.addItem-btn button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
}
.addItem-btn h3 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.addItem-total {
  display: flex;
  flex-direction: row;
  margin-left: 4px;
}
.payment-summary {
  flex: 0 0 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #000;
}
.total-summary {
  flex: 0 0 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: black;
  border-radius: 0.25rem;
  color: #fff;
}

.btn-total-cancel {
  flex: 0 0 50%;
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  cursor: pointer;
  background-color: black;
  color: #fff;
  border-color: transparent;
  font-weight: bold;
  letter-spacing: 0.1rem;
  transition: all 0.3s linear;
  border-radius: 0.25rem;
}
.btn-total-cancel:hover {
  background-color: #fff;
  color: #000;
  border-radius: 0.25rem;
}
.total-summary p,
.total-summary h3 {
  color: yellow;
}

.btn-confirm-order {
  flex: 0 0 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  background-color: blue;
  color: yellow;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3 linear;
  letter-spacing: 0.05rem;
}
/*모바일 세로*/
@media screen and (max-width: 479.99px) {
  .addItem-move {
    flex: 0 0 20px;
    padding: 2px;
  }
  .addItem-list {
    /* padding: 0 1px; */
    padding: 1px;
  }
  .addItem-article {
    flex: 0 0 100px;
    margin: 0 2px;
    /* border: 1px solid blue; */
    /**/
    flex: 0 0 120px;
  }
  .addItem-remove {
    top: 4px;
    right: 4px;
    padding: 0.125rem;
    font-size: 0.785rem;
  }

  .addItem-title {
    /* flex: 0 0 30%; */
    font-size: 0.8rem;
  }

  .addItem-btn button {
    font-size: 0.8rem;
    /* border: 1px solid red; */
  }
  .addItem-btn h3 {
    font-size: 0.8rem;
  }
  .addItem-total {
    flex: 0 0 90px;
    flex-direction: column;
  }
  .btn-total-cancel {
    display: none;
  }
  .total-summary p {
    font-size: 0.8rem;
  }
  .total-summary h3 {
    font-size: 1rem;
  }
  .btn-confirm-order {
    font-size: 1rem;
  }
}
