body.modal-open {
  overflow: hidden;
}

.modal {
  display: none;
}

.modal-section {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #f0f0f0;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  z-index: 999;
  overflow: hidden;
}
.modal-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  border-radius: 0.25rem;
  background-color: #fff;
}
.modal-item::-webkit-scrollbar,
.modal-info::-webkit-scrollbar {
  display: none;
}
.modal-item,
.modal-info {
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}
.modal-item {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 6px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 0.25rem;
  overflow: auto;
}
.modal-menuItem {
  display: flex;
  overflow: hidden;
}
.single-img {
  max-height: 100%;
  max-width: 100%;
  border-radius: 1.25rem;
}
.modal-title {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.modal-title h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  letter-spacing: 0.1rem;
}
.modal-title span {
  text-transform: capitalize;
}
.hot-iced {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s linear;
}
.hot-iced button {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  background-color: transparent;
  text-transform: uppercase;
  border-radius: 5rem;
  cursor: pointer;
}

.order-hot {
  color: red;
  border: 2px solid red;
}
.order-iced {
  color: blue;
  border: 2px solid blue;
}
.order-hot:hover {
  background-color: red;
  color: #fff;
}
.order-iced:hover {
  background-color: blue;
  color: #fff;
}
.order-iced.active-iced {
  background-color: blue;
  color: #fff;
}
.order-hot.active-hot {
  background-color: red;
  color: #fff;
}
.modal-info {
  flex-grow: 1;
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 4px;
  transition: all 0.3s linear;
  overflow-y: auto;
}
.coffee-row {
  display: flex;
  align-items: center;
}
.coffee-row > h4 {
  display: flex;
  align-items: center;
  height: 100%;
}
.coffee-option {
  display: flex;
  align-items: center;
  height: 100%;
}
.coffee-option h4 {
  display: flex;
  align-items: center;
  height: 100%;
}
.coffee-option h5 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.btn-row {
  flex: 1;
  display: flex;
  justify-content: space-evenly;
  height: 100%;
  border: 0.25rem;
  background-color: #e0e0e0;
}
.btn-row .minus,
.btn-row .plus {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
  color: #000;
}
.btn-row h3 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.coffee-size {
  flex: 1;
  font-weight: bold;
  text-transform: uppercase;
  color: #000;
  background-color: transparent;
  border-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.coffee-size:hover {
  background-color: #000;
  color: #fff;
}
.active-size {
  background-color: black;
  color: #fff;
}
.modal-price {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
.modal-price h3 {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
.order-btn {
  display: flex;
  justify-content: center;
  border: 0.25rem;
  padding: 0.3rem;
}
.order-btn button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  letter-spacing: 0.05rem;
  background-color: #e0e0e0;
  border-color: transparent;
  color: #000;
  border-radius: 2.5rem;
  cursor: pointer;
}
.order-close-btn:hover,
.order-select-btn:hover {
  background-color: black;
  color: #fff;
}
.order-select-btn:disabled {
  background-color: gray;
  color: darkgray;
  border: 2px solid darkgray;
  cursor: default;
}
.order-select-btn:disabled:hover {
  background-color: gray;
  color: darkgray;
}
/*모바일 세로*/
@media screen and (max-width: 479.99px) {
  .modal-section {
    max-width: 600px;
    width: 85vw;
    height: 85vh;
    padding: 0.75rem 0.5rem;
  }
  .modal-menuItem {
    flex: 0 0 140px;
    gap: 0.3px;
    padding: 0.3px;
    margin-bottom: 0.2rem;
  }
  .single-img {
    flex: 0 0 40%;
  }
  .modal-title h3 {
    padding: 2px;
    margin-bottom: 0.125rem;
    font-size: 0.95rem;
  }
  .modal-title span {
    font-size: 0.875rem;
  }
  .hot-iced {
    flex: 0 0 45px;
    margin-bottom: 0.2rem;
    padding: 5px 0;
  }
  .hot-iced button {
    width: 80px;
    margin: 0 14px;
    font-size: 1rem;
  }
  .coffee-row {
    flex: 0 0 60px;
    flex-direction: column;
    margin-bottom: 3px;
    border: none;
  }
  .coffee-row > h4 {
    flex: 0 0 30px;
    display: flex;
    justify-content: center;
    width: 100%;
    font-size: 1rem;
    margin-bottom: 2px;
  }
  .coffee-option {
    flex: 0 0 30px;
    justify-content: center;
    width: 100%;
  }
  .coffee-option h4 {
    justify-content: center;
    font-size: 1rem;
  }
  .coffee-option h5 {
    font-size: 0.85rem;
  }
  .btn-row {
    flex: 0 0 30px;
    width: 100%;
  }
  .btn-row .minus,
  .btn-row .plus {
    font-size: 1rem;
  }
  .btn-row h3 {
    font-size: 1rem;
  }
  .coffee-size {
    font-size: 0.9rem;
  }
  .modal-price {
    flex: 0 0 40px;
    margin: 2px 0;
  }
  .modal-price h3 {
    font-size: 1.1rem;
  }
  .order-btn {
    flex: 0 0 50px;
    padding: 0.3rem;
  }
  .order-btn button {
    width: 110px;
    margin: 0 20px;
    font-size: 1rem;
  }
}
