.nav-container {
  display: flex;
  position: relative;
  height: 100%;
  background-color: #fff;
  padding: 0.15rem;
}

.nav-center {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.nav-center h2 {
  color: #333;
  font-weight: bold;
  letter-spacing: 0.25rem;
  text-transform: capitalize;
  /* border: 1px solid red; */
}
.cafe-logo {
  margin-right: 0.25rem;
  object-fit: cover;
}
.item-details {
  display: none;
}
.show-item-details {
  display: flex;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  justify-content: center;
  align-items: center;
  /* border: 1px solid red; */
}
.show-item-details button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #000;
  font-weight: bold;
  background-color: transparent;
  border: 2px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 0.3s linear;
}
.show-item-details button:hover {
  background-color: #000;
  color: #fff;
}
/*모바일 세로*/
@media screen and (max-width: 479.99px) {
  .nav-container {
    justify-content: center;
    align-items: center;
    padding: 0.15rem;
  }
  .nav-center h2 {
    font-size: 1.785rem;
  }
  .cafe-logo {
    width: 70px;
    height: 70px;
  }
  .show-item-details {
    position: static;
    flex: 0 0 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(0);
    width: 50px;
    height: 50px;
    margin: 0.125rem;
    padding: 0.3rem;
    /* padding: 10px 15px; */
  }
  .show-item-details button {
    width: 100%;
    height: 100%;
    font-size: 0.785rem;
    padding: 0.125rem;
  }
}
/*모바일 가로 & 태블릿 세로*/
@media screen and (min-width: 480px) {
  .nav-container {
    padding: 0.25rem;
  }

  .nav-center h2 {
    /* font-size: 2.125rem; */
    font-size: 2rem;
  }

  .cafe-logo {
    width: 80px;
    height: 80px;
  }
  .show-item-details {
    position: absolute;
    transform: translateY(-50%);
    right: 10px;
    width: 60px;
    height: 60px;
    margin: 0.125rem;
    padding: 0.5rem;
  }

  .show-item-details button {
    width: 100%;
    height: 100%;
    font-size: 0.9rem;
  }
}
/*태블릿 가로*/
@media screen and (min-width: 768px) {
  .nav-container {
    padding: 0.35rem;
  }
  .nav-center h2 {
    font-size: 2.25rem;
  }
  .cafe-logo {
    width: 90px;
    height: 90px;
  }
  .show-item-details {
    right: 15px;
    width: 60px;
    height: 60px;
    margin: 0.2rem;
    padding: 0.3rem;
  }

  .show-item-details button {
    width: 100%;
    height: 100%;
    font-size: 1rem;
  }
}
/*노트북 & 태블릿 가로*/
@media screen and (min-width: 1024px) {
  .nav-container {
    padding: 0.35rem;
  }
  .nav-center h2 {
    font-size: 2.5rem;
  }
  .cafe-logo {
    width: 95px;
    height: 95px;
  }
  .show-item-details {
    right: 18px;
    width: 70px;
    height: 70px;
    padding: 0.25rem;
    margin: 0.25rem;
  }
  .show-item-details button {
    padding: 0.25rem;
    width: 100%;
    height: 100%;
    font-size: 1.1rem;
  }
}

