.completed-items {
  z-index: 999;
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid rgba(0, 0, 0, 0.5);
  background-color: #f0f0f0;
  border-radius: 0.25rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.completed-center {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-radius: 0.25rem; */
  /* border: 1px solid rgba(0, 0, 0, 0.3); */
}
.completed-center p {
  display: flex;
  justify-content: center;
  align-items: center;
}
.complete-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e0e0e0;
  border-color: transparent;
  border-radius: 0.25rem;
  letter-spacing: 0.1rem;
  transition: all 0.3s linear;
  cursor: pointer;
  color: #000;
}

.complete-btn:hover {
  background-color: #000;
  color: #fff;
}

/*모바일 세로*/
@media screen and (max-width: 479.99px) {
  .completed-items {
    max-width: 600px;
    width: 95vw;
    height: 95vh;
    padding: 0.75rem 0.5rem;
  }

  .completed-center {
    margin-bottom: 4px;
  }

  .completed-center p {
    padding: 0.5rem 0.3rem;
    font-size: 1.5rem;
  }

  .complete-btn {
    flex: 0 0 50px;
  }
  .complete-btn h2 {
    font-size: 1.1rem;
    padding: 0.125rem;
  }
}
/*모바일 가로 & 태블릿 세로*/
@media screen and (min-width: 480px) {
  .completed-items {
    max-width: 600px;
    width: 95vw;
    height: 95vh;
    padding: 0.75rem 0.5rem;
  }

  .completed-center {
    margin-bottom: 4px;
  }

  .completed-center p {
    padding: 0.5rem 0.3rem;
    font-size: 2.25rem;
  }

  .complete-btn {
    flex: 0 0 60px;
  }
  .complete-btn h2 {
    font-size: 1.3rem;
    padding: 0.125rem;
  }
}
/*태블릿 가로*/
@media screen and (min-width: 768px) {
  .completed-items {
    max-width: 700px;
    width: 90vw;
    height: 90vh;
    padding: 1rem 0.75rem;
  }

  .completed-center {
    margin-bottom: 4px;
  }

  .completed-center p {
    padding: 0.75rem 0.4rem;
    font-size: 2.5rem;
  }
  .complete-btn {
    flex: 0 0 70px;
  }
  .complete-btn h2 {
    font-size: 1.5rem;
    padding: 0.2rem;
  }
}
/*노트북 & 태블릿 가로*/
@media screen and (min-width: 1024px) {
  .completed-items {
    max-width: 800px;
    width: 85vw;
    height: 85vh;
    padding: 1.25rem 1rem;
  }
  .completed-center {
    margin-bottom: 4px;
  }
  .completed-center p {
    padding: 1rem 0.5rem;
    font-size: 2.75rem;
  }
  .complete-btn {
    flex: 0 0 80px;
  }
  .complete-btn h2 {
    font-size: 1.75rem;
    padding: 0.25rem;
  }
}
