.item-article {
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background-color: #fff;
  border-radius: 0.25rem;
  transition: all 0.3s linear;
  cursor: pointer;
  /* border: 1px solid red; */
}

.item-article:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 1);
}
.item-container {
  height: 100%;
}
.item-center {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.item-center img {
  width: 100%;
  object-fit: fill;
  border-radius: 5px;
}
.item-info {
  display: flex;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 2px;
  margin-bottom: 2px;
  margin-top: 2px;
}

.item-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.itemAdd-btn {
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  letter-spacing: 3px;
  border-color: transparent;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s linear;
}
.itemAdd-btn:hover {
  background-color: #0056b3;
  color: #fff;
  border-radius: 0.25rem;
}
/*모바일 세로*/
@media screen and (max-width: 479.99px) {
  .item-article {
    padding: 3px;
  }
  .item-center img {
    height: 145px;
    margin-bottom: 2px;
  }
  .item-info p {
    font-size: 0.8rem;
  }
  .itemAdd-btn {
    margin-top: 2px;
    margin-bottom: 2px;
    padding: 3px;
  }
}
/*모바일 가로 & 태블릿 세로*/
@media screen and (min-width: 480px) {
  .item-article {
    padding: 3px;
  }
  .item-center img {
    height: 155px;
    margin-bottom: 2px;
  }
  .item-info p {
    font-size: 0.9rem;
  }
  .itemAdd-btn {
    margin-top: 2px;
    margin-bottom: 2px;
    padding: 3px;
  }
}
/*태블릿 가로*/
@media screen and (min-width: 768px) {
  .item-article {
    padding: 4px;
  }
  .item-center img {
    height: 170px;
    margin-bottom: 3px;
  }
  .item-info p {
    font-size: 1rem;
  }
  .itemAdd-btn {
    margin-top: 3px;
    margin-bottom: 3px;
    padding: 5px;
  }
}
/*노트북 & 태블릿 가로*/
@media screen and (min-width: 1024px) {
  .item-article {
    padding: 5px;
  }
  .item-center img {
    height: 190px;
    margin-bottom: 4px;
  }
  .item-info p {
    font-size: 1.1rem;
  }
  .itemAdd-btn {
    margin-top: 4px;
    margin-bottom: 4px;
    padding: 6px;
  }
}
