.sidebar-container {
  display: flex;
  width: 100%;
  height: 100%;
}

.filter-btn {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  color: black;
  font-weight: bold;
  border-radius: 0.25rem;
  cursor: pointer;
  letter-spacing: 0.1rem;
}
.active {
  background-color: black;
  color: #fff;
}
.filter-btn:hover {
  background-color: lightgray;
}
.filter-btn.active {
  background-color: black;
  color: #fff;
}

/*모바일 세로*/
@media screen and (max-width: 479.99px) {
  .sidebar-container {
    padding: 0 0.125rem;
    margin-bottom: 0.25rem;
  }
  .category-title {
    display: none;
  }
  .filter-btn {
    flex: 1 auto;
    margin: 0 2px;
    background-color: #e0e0e0;
    /**/
    font-size: 0.9rem;
  }
}
/*모바일 가로 & 태블릿 세로*/
@media screen and (min-width: 480px) {
  .sidebar-container {
    /* height: 37.5px; */
    padding: 0 0.125rem;
    /* margin: 2px 0; */
  }
  .category-title {
    display: none;
  }
  .filter-btn {
    flex: 1 auto;
    margin: 0 2px;
    background-color: #e0e0e0;
    /**/
    font-size: 0.9rem;
  }
}
/*태블릿 가로*/
@media screen and (min-width: 768px) {
  .sidebar-container {
    /* height: 40px; */
    margin: 2px 0;
  }
  .category-title {
    display: none;
  }
  .filter-btn {
    font-size: 1rem;
    margin: 0 3px;
    /* border: 1px solid rgba(0, 0, 0, 0.5); */
    background-color: #e0e0e0;
  }
}
/*노트북 & 태블릿 가로*/
@media screen and (min-width: 1024px) {
  .sidebar-container {
    height: 100%;
    flex-direction: column;
    align-items: center;
    margin-top: 0;
  }
  .category-title {
    flex: 0 0 110px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-weight: bold;
    font-size: 1.5rem;
    background-color: #efefef;
    color: #000;
  }
  .filter-btn {
    flex: 0 0 70px;
    width: 100%;
    margin: 0;
    margin-top: 6px;
    padding: 12px;
    font-size: 1.3rem;
    font-weight: bold;
    border: 1px solid #ccc;
    background-color: #f0f0f0;
    border-radius: 0.5rem;
  }
}
