.order-details {
  display: none;
}

.show-order-details {
  z-index: 999;
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #f0f0f0;
  border: 2px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}

.details-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  background-color: #fff;
}
.details-container > h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  letter-spacing: 0.1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.details-items-info::-webkit-scrollbar,
.detail-item-center::-webkit-scrollbar {
  display: none;
}
.details-items-info,
.detail-item-center {
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}
.details-items-info {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  overflow: auto;
  border-radius: 0.25rem;
  padding: 0.2rem;
}

.detail-item {
  display: grid;
  grid-auto-rows: 100%;
  border-radius: 0.25rem;
  border: 1px solid rgba(0, 0, 0, 0.3);
}
.detail-img {
  margin: auto;
  border-radius: 0.25rem;
}
.detail-item-info {
  display: flex;
  flex-direction: column;
  border: 0.25rem;
}
.detail-item-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.detail-item-title > span {
  text-transform: capitalize;
}
.detail-item-center {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
  border-radius: 0.25rem;
}
.detail-row {
  display: flex;
  padding: 2px;
  /* margin-bottom: 4px; */
  gap: 4px;
}
.detail-row h3 {
  flex: 1;
  display: flex;
  /* justify-content: center; */
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  /* border: 1px solid blue; */
}
.detail-row p {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
}
.order-detail-price {
  display: flex;
  flex-direction: column;
}
.order-detail-price h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  flex: 0 0 50%;
}

.order-detail-check {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
  font-weight: bold;
  background-color: transparent;
  border-color: transparent;
  background-color: #e0e0e0;
  cursor: pointer;
  border-radius: 0.25rem;
  transition: all 0.3s linear;
}
.order-detail-check:hover {
  background-color: #000;
  color: #fff;
}
